body {
  /* background: #86109b; */
  /* background: #0f0e17; */
  background: url(/src/backgroundWallpaper.jpg);
  background-size: cover;
  background-attachment: scroll;
  background-size: 100% auto;
  background-repeat: no-repeat;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
}

.logo {
  display: block;
  margin-left: auto;
  margin-right: 425px;
  padding-top: 40px;

}

.input-text {
  display: inline;
  margin: 0;
  border: none;
  border-radius: 0.4rem;
  padding: 10px;
}

.list ul li button {
  position: absolute;
  right: 0%;
  background: #f34541;
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
  border-radius: 0.4rem;
  display: inline-block;
  border: none;
}

.add-btn {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  /* background: #7313cb; */
  background: #ff8906;
  margin: 20px;
  padding: 10px;
  border-radius: 0.4rem;
  display: inline-block;
  border: none;
}

