ul {
  /* remove default padding and margin from ul*/
  margin: 0px;
  padding: 0px;
}
.list ul li {
  display: block;
  width: 100%;
  text-decoration: none;
  color: #000000;
  background-color: #ffffff;
  line-height: 30px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #cccccc;
  padding: 10px;
  position: relative;
}

.container {
  color: #fff;
  max-width: 500px;
  margin: auto;
  padding-top: 70px;
  
}

.main-text{
  text-align: center;
}

.todoContainer{
  text-align: center;
}

h1{
  display:inline
}


.header img {
  float: right;

}

 h1 {
